.App-Wrapper {
  text-align: center;
  height: 100%;
  width: 100%;
}

.App-Controls {
  position: fixed;
  top: 50px;
  left: 50px;
  max-width: 220px;
  width: 100%;
}

.App-ControlsInput {
  display: block;
  margin-bottom: 20px;
}




.Input-Control {
  box-sizing: border-box;
  padding: 10px 10px;
  line-height: 20px;
  font-size: 18px;
  outline: none;
  width: 100%;
  height: 42px;
  border-radius: 4px;
  border: 1px solid #f2f2f2;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, .2);
}

.Input {
  position: relative;
}

.Input_error .Input-Control {
  border: #cf3737 solid 1px;
}

.Input_error:hover::after {
  position: absolute;
  display: block;
  content: '\412\432\435\434\438\442\435   \43A\43E\440\440\435\43A\442\43D\44B\439   HEX \43A\43E\434';
  right: -100%;
  top: 50%;
  -webkit-transform: translate(10px, -50%);
          transform: translate(10px, -50%);
  height: 20px;
  padding: 5px 10px;
  background-color: #cf3737;
  border-radius: 4px;
  font-size: 14px;
  line-height: 20px;
  color: #fff;
}
.Button {
  height: 42px;
  padding: 5px 40px;
  line-height: 20px;
  font-size: 18px;
  border: none;
  outline: none;
  border-radius: 4px;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, .2);
  background-color: #ff630f;
  color: #fff;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html,
body,
.App {
  width: 100%;
  height: 100%;
}
